
/*------------------------------------------
        Landing overlay Start Here
-------------------------------------------*/

.overlay-landing{
    .modal-content-inr{
        .accordion{
            margin: 36px 0;
        }
        .action{
            margin: 0;
        }
    }
}

/*------------------------------------------
        Landing overlay End Here
-------------------------------------------*/

/*------------------------------------------
        Landing navbar Start Here
-------------------------------------------*/

.nav-landing{
    padding: 32px 0;
    .container-fluid{
        .wrapper{
            display: flex;
            align-items: center;
            justify-content: space-between;
            .logo-otr{
                .logo{
                    width: 170px;
                }
            }
            .menu-otr{
                @include breakpoint($secreen-max-lg){
                    display: none;
                }
                .menu-ul{
                    display: flex;
                    align-items: center;
                    .menu-li{
                        &:not(:last-child){
                            margin-right: 40px;
                        }
                        .menu-a{
                            color: $White-color;
                            transition: .3s;
                            &:hover{
                                color: $Primary-color;
                            }
                        }
                    }
                }
            }
            .action-otr{
                display: flex;
                @include breakpoint($secreen-max-lg){
                    display: none;
                }
            }
            .burger-icon-otr{
                display: none;
                align-items: center;
                justify-content: center;
                width: 56px;
                height: 56px;
                border-radius: 100%;
                background-color: $White-Bg-1;
                cursor: pointer;
                @include breakpoint($secreen-max-lg){
                    display: flex;
                }
                @include breakpoint($secreen-xs){
                    width: 40px;
                    height: 40px;
                }
                .burger-icon{
                    font-size: 24px;
                    color: $White-color;
                    @include breakpoint($secreen-xs){
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

/*------------------------------------------
        Landing navbar End Here
-------------------------------------------*/

/*------------------------------------------
        Landing Hero Start Here
-------------------------------------------*/

.landing-Hero{
    position: relative;
    overflow: hidden;
    background: linear-gradient(252.07deg, rgba(1, 156, 234, 0.050) 0%, rgba(255, 255, 255, 0.050) 100%);
    &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-image: url(/assets/img/footer-bg-img.png);
        top: 0;
        left: 0;
        opacity: .6;
    }
    .container-fluid{
        .row-custom{
            align-items: center;
            position: relative;
            padding: 96px 0 128px 0;
            z-index: 2;
            .col-content-otr{
                .col-content-inr{
                    .heading{
                        color: $White-color;
                        .text-color{
                            color: $Primary-color;
                        }
                    }
                    .heading2-otr{
                        display: inline-flex;
                        background-color: $Dark-bg;
                        border-radius: 16px;
                        margin: 40px 0 60px 0;
                        .heading2{
                            color: $White-color;
                            padding: 12px 24px;
                        }
                    }
                    .logo-otr{
                        .logo-ul{
                            display: flex;
                            align-items: center;
                            flex-wrap: wrap;
                            .logo-li{
                                @include breakpoint($secreen-xxs){
                                    margin-top: 16px;
                                }
                                &:not(:last-child){
                                    margin-right: 16px;
                                }
                            }
                        }
                    }
                }
            }
            .col-img-otr{
                .col-img-inr{
                    @include breakpoint($secreen-max-md){
                        padding-top: 72px;
                    }
                    .img{
                        width: 100%;
                    }
                }
            }
        }
    }
