/*------------------------------------------
        Home pages Start Here
-------------------------------------------*/

.home-link-main{
    margin: 128px 0;
    .container-fluid{
        .wrapper{
            margin-bottom: 72px;
            .tab-otr{
                display: flex;
                align-items: center;
                @include breakpoint($secreen-max-md){
                    flex-direction: column;
                    align-items: flex-start;
                }
                .heading{
                    color: $White-color;
                    margin-right: 24px;
                    @include breakpoint($secreen-max-md){
                        margin: 0 0 24px 0;
                    }
                }
                .tab-inr{
                    position: relative;
                    .btn-drop{
                        display: flex;
                        align-items: center;
                        background-color: $White-Bg-1;
                        border: 2px solid $Primary-color;
                        border-radius: 12px;
                        padding: 6px 16px;
                        cursor: pointer;
                        .btn-drop-inr{
                            color: $White-color;
                        }
                        .arrow-down{
                            font-size: 18px;
                            color: $White-color;
                            margin: 0 0 0 8px;
                        }
                        .change-text{
                            color: $White-color;
                        }
                    }
                    .tabs-ul{
                        position: absolute;
                        left: 0;
                        top: 80px;
                        width: 200px;
                        border-radius: 24px;
                        background-color: $Primary-shade;
                        padding: 16px 24px 20px 24px;
                        opacity: 0;
                        visibility: hidden;
                        transition: .3s;
                        z-index: 10;
                        .tab-link{
                            cursor: pointer;
                            &:not(:last-child){
                                margin-bottom: 12px;
                            }
                            .tab-p{
                                color: $White-color;
                            }
                        }
                        .active{
                            .tab-p{
                                color: $Primary-color;
                            }
                        }
                    }
                    .tab-drop-open{
                        opacity: 1;
                        visibility: visible;
                        top: 50px;
                    }
                }
            }
        }
        .row-custom-main{
            .tab-content{
                display: none;
                opacity: 0;
                animation: fadeIn 0.3s ease 1 forwards;
                .row-custom-inr{
                    justify-content: center;
                    .col-linkk-otr{
                        .col-linkk-inr{
                            .img-otr{
                                margin-bottom: 24px;
                                border-radius: 30px;
                                border: 10px solid $Primary-shade;
                                overflow: hidden;
                                .img-linkk{
                                    width: 100%;
                                }
                            }
                            .linkk-page{
                                text-align: center;
                                color: $White-color;
                            }
                        }
                        .box-1,.box-2{
                            margin-bottom: 30px;
                        }
                        .box-1,.box-2,.box-3,.box-4{
                            @include breakpoint($secreen-max-md){
                                margin-bottom: 30px;
                            }
                        }
                    }
                }
                .row-custom-inr2{
                    margin: 128px 0 0 0;
                    justify-content: center;
                    .wrapper{
                        margin-bottom: 72px;
                        .heading{
                            color: $White-color;
                        }
                    }
                    .col-linkk-otr{
                        .col-linkk-inr{
                            .img-otr{
                                margin-bottom: 24px;
                                border-radius: 30px;
                                border: 10px solid $Primary-shade;
                                overflow: hidden;
                                .img-linkk{
                                    width: 100%;
                                }
                            }
                            .linkk-page{
                                text-align: center;
                                color: $White-color;
                            }
                        }
                        .box-1,.box-2,.box-3,.box-4,.box-5,.box-6,.box-7,.box-8,.box-9,.box-10,.box-11,.box-12,.box-13,.box-14,.box-15,.box-16,.box-17,.box-18{
                            margin-bottom: 30px;
                        }
                        .box-1,.box-2,.box-3,.box-4,.box-5,.box-6,.box-7,.box-8,.box-9,.box-10,.box-11,.box-12,.box-13,.box-14,.box-15,.box-16,.box-17,.box-18,.box-19{
                            @include breakpoint($secreen-max-sm){
                                margin-bottom: 30px;
                            }
                        }
                    }
                }
            }
            .active{
                display: block;
                opacity: 1;
            }
        }
    }
}

/*------------------------------------------
        Home pages End Here
-------------------------------------------*/

