    position: relative;
    overflow: hidden;
    background: linear-gradient(252.07deg, rgba(1, 156, 234, 0.050) 0%, rgba(255, 255, 255, 0.050) 100%);
    &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-image: url(/assets/img/footer-bg-img.png);
        top: 0;
        left: 0;
        opacity: .6;
    }
    .container-fluid{
        .wrapper{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            text-align: center;
            padding: 128px 0;
            z-index: 2;
            .heading{
                color: $White-color;
            }
            .desc{
                color: $Shade-1;
                margin: 16px 0 40px 0;
            }
            .action-otr{
